

import React, { Component, useEffect, useState } from 'react';
import { Modal } from 'antd'
import Form from '../../components/form/form'
import InputType from '../../components/form/form-controls/input'
import Button from '../../components/form/button/button'
import ErrorBox from '../../components/form/error-box/error-box'
import Loader from '../../components/ui/loader/loader'
import { forgotPassword } from '../../services/api/user'

const ForgotPassword = (props) => {

    const [email, setemail] = useState('');
    const [emailWarn, setemailWarn] = useState(false);
    const [errorMsg, seterrorMsg] = useState('');
    const [showLoader, setshowLoader] = useState(false);

    const success = () => {
        Modal.success({
            title: 'Successful',
            content: 'Your new password has been sent to your email.',
        });
    }

    const callback = (data) => {
        if (data.status === 200) {
            reset();
            success();
            props.cancelClick(); // close modal
        } else if (data.response.status === 404) {
            seterrorMsg(data.response.data.statusText)
            setshowLoader(false)
        } else {
            seterrorMsg('Something went wrong. Please try again later &#x2639;')
            setshowLoader(false)
        }
    }

    const onFormSubmit = (e) => {
        e.preventDefault();
        reset();
        setshowLoader(true)
        if (validate()) {
            outlineColor();
            const data = {
                email: email
            };
            if (data) {
                forgotPassword(callback, data);
            }
        } else {
            outlineColor()
            seterrorMsg('Please fill all the required field')
            setshowLoader(false)
        }
    }

    const onInputChange = (e) => {
        e.target.name(e.target.value)
    }

    const reset = () => {
        setemail('')
        seterrorMsg('')
        setshowLoader(false)
    }

    const outlineColor = () => {
        if (!email.trim()) {
            setemailWarn(true)
        } else {
            setemailWarn(false)
        }
    }

    const validate = () => {
        return email.trim() !== '';
    }

    return (
        <Form onSubmitHandler={onFormSubmit}>
            <InputType
                type="email"
                value={email}
                name="email"
                labelName="Email"
                placeholder="Enter Your Email"
                onInputChange={onInputChange}
                isRequired={true}
                classValue={emailWarn ? 'inputField-outline' : null}
            />
            { errorMsg ? <ErrorBox errorMsgs={errorMsg} /> : null}
            { showLoader ? <Loader>Requesting...</Loader> : null}
            <div className="issue-form-button" >
                <Button isType='primary' htmlTypes='submit' isBlock={true}>Submit</Button>
            </div>
        </Form>
    )

}

export default ForgotPassword