

import React, { Component, useEffect, useState } from 'react'
import { Layout } from 'antd'
import Modal from '../components/layout/modal/modal';
import Header from '../components/header/header';
import Heading from '../components/form/heading/heading'
import InputType from '../components/form/form-controls/input'
import Button from '../components/form/button/button'
import Form from '../components/form/form'
import { login, tokenVerify } from '../services/api/login-ims'
import ForgotPassword from './forgot_password/forgot_password'
import { SetToken, GetToken } from '../helpers/token';
import * as actionType from '../store/actions/action-type';
import { connect } from 'react-redux'
import ErrorBox from '../components/form/error-box/error-box'
import { Redirect } from 'react-router-dom'
import Loader from '../components/ui/loader/loader';
const { Content, Footer } = Layout;

const Login = (props) => {

    const [visible, setvisible] = useState(false);
    const [userName, setuserName] = useState('');
    const [password, setpassword] = useState('');
    const [url, seturl] = useState(props.redirectURL);
    const [errorMsg, seterrorMsg] = useState('');
    const [showLoader, setshowLoader] = useState(false);
    const [loginLoader, setloginLoader] = useState(true);

    const tokenVerifyCallback = (data) => {
        if (data.status === 200) {
            setloginLoader(false);
            props.basic(data.data);
        } else {
            setloginLoader(false);
            console.log(data.response)
        }
    }

    useEffect(() => {
        if (GetToken) {
            tokenVerify(tokenVerifyCallback, GetToken());
        } else {
            setloginLoader(false);
        }
    },[])

    const callback = (data) => {
        if (data.status === 200) {

            setshowLoader(false);
            seterrorMsg('');
            sessionStorage.setItem('jwt', data.data.auth_token);
            // SetToken(data.data.token, 5);
            props.basic(data.data.user);
            if (data.data.user.firstTime === false) {
                props.history.push('/user/updatePassword');
            } else if ((data.data.user.approval === 'pending' || data.data.user.approval === null) && data.data.user.firstTime === true) {
                props.history.push('/user/profile');
            } else if (data.data.user.firstTime && data.data.user.approval === 'approved') {
                props.login();
                if (!url || url === '/') {
                    props.history.push('/dashboard');
                } else {
                    props.history.push(url);
                }
            }
        } else if (data.response) {
            setshowLoader(false)
            seterrorMsg("Please enter valid username or password.")
        } else {
            setshowLoader(false)
            seterrorMsg("Something went wrong. Please try again latter!")
        }
    }

    const onFormSubmit = (e) => {
        e.preventDefault()
        setshowLoader(true)
        seterrorMsg("")
        const data = {
            email: userName,
            password: password
        };
        if (userName && password) {
            login(callback, data);
        } else {
            seterrorMsg("Please enter valid username or password")
        }
    }

    const onInputChange = (e) => {
        e.target.name(e.target.value)
    }

    const openModal = () => {
        setvisible(true)
    }

    // close modal
    const cancelClick = () => {
        setvisible(false)
    }

    if (loginLoader) {
        return (
            <Layout>
                <Header />
                <Layout style={{ marginTop: 64, height: '93vh' }} >
                    <Content className="login">
                        <Loader>Loading...</Loader>
                    </Content>
                </Layout>
            </Layout>
        )
    } else if (props.isLoggedIn && props.isApproved === 'approved' && GetToken()) {
        return <Redirect to={url} />
    } else {
        return (
            <Layout>
                <Header />
                <Layout style={{ marginTop: 64, height: '93vh' }} >
                    <Modal
                        heading="Enter Your Registered Email."
                        showModal={visible}
                        cancelClick={cancelClick}
                        footer={null}
                    >
                        <ForgotPassword cancelClick={cancelClick} />
                    </Modal>
                    <Content className="login">
                        <div className="login-box">
                            <Heading heading={<b>Sign In</b>} />
                            <Form onSubmitHandler={onFormSubmit}>
                                <InputType
                                    type="text"
                                    value={userName}
                                    name="userName"
                                    prefixType="user"
                                    placeholder="Enter Username"
                                    onInputChange={onInputChange}
                                    isRequired={true}
                                    labelName="User Name"
                                />
                                <InputType
                                    type="password"
                                    value={password}
                                    name="password"
                                    prefixType="lock"
                                    placeholder="Enter Password"
                                    onInputChange={onInputChange}
                                    isRequired={true}
                                    labelName="Password"
                                />
                                {errorMsg ? <ErrorBox errorMsgs={errorMsg} /> : null}
                                {showLoader ? <Loader>Logging...</Loader> : null}
                                <div className="login-button-group">
                                    <div className="login-group">
                                        <Button isType='primary' htmlTypes='submit' isBlock={true}>LOGIN</Button>
                                    </div>
                                    <div className="login-group">
                                        <Button isType='danger' isBlock={true} onClick={openModal}>FORGOT PASSWORD</Button>
                                    </div>
                                </div>
                            </Form>
                        </div>
                    </Content>
                    <Footer style={{ textAlign: 'center', marginBottom: 64 }}>
                        Copyright Â© 2018. Ethereal Machines Pvt Ltd. All rights reserved.
                        </Footer>
                </Layout>
            </Layout>
        )
    }

}



const mapStateToProps = (state) => {
    let islogedin, redirectURL;
    if (state.Login === undefined || state.Login === null) {
        islogedin = false,
            redirectURL = '/loginasims'
    }
    else {
        islogedin = state.Login.isLogedIn
        redirectURL = state.Login.redirect
    }
    return {
        isLoggedIn: islogedin,
        isApproved: state.Basic.basic.approval,
        redirectURL: redirectURL
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        login: () => {
            dispatch({
                type: actionType.LOGINASIMS
            })
        },
        basic: (data) => {
            dispatch({
                type: actionType.USER_BASIC,
                value: data
            })
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Login)